@use 'base';

.form-row {
    margin-top: 22px;
    padding: 16px;
    gap: 10px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    background-color: base.$background-color;
    border-radius: 5px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.04), 0 2px 6px rgba(0, 0, 0, 0.04), 0 0 1px rgba(0, 0, 0, 0.04);
}

.form-control {
    border-radius: 2px;
    border-collapse: collapse;
    height: 28px;
    border: none;
    padding-left: 8px;
    font-weight: 500;
    font-size: 11px;
    line-height: 13px;
    flex-grow: 1;
}

.error {
    border-bottom: 1px red solid;
}

.submit-btn {
    height: 28px;
    border: none;
    background-color: white;
    border-radius: 10px;
    flex-grow: 1;

    &:hover {
        background-color: base.$primary-color;
        color: white;
    }
}
